
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 20px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">轮渡线路</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">

                <el-row>
                <el-col :span="4" class="el-col"  style="margin-left: 0">
                    <p class="input-title">班次:</p>
                    <el-input  v-model="searchForm.shiftNum" placeholder="请输入班次">
                    </el-input>
                </el-col>

                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-search" @click = "search">查询</el-button>
                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "add">新增</el-button>
                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "seat">库存维护</el-button>
                </el-col>
            </el-row>
        </el-col>


        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>班次</th>
                    <th>出发地</th>
                    <th>到达地</th>
                    <th>库存</th>
                    <th>库存维护到期时间</th>
                    <th>票价</th>
                    <th>操作</th>

                </tr>
                <tr v-for="(item,index) in aaData.data">
                    <td>{{item.shiftNum}}</td>
                    <td>{{item.depStationName}}</td>
                    <td>{{item.destStationName}}</td>
                    <td>{{item.totalSeatNum}}</td>
                    <td>{{item.lastDepDate}}</td>
                    <td>{{item.ticketPrice}}</td>
                    <td>
                        <span class="dothis" @click="edit(item.id,item.shiftNum,item.supplierDepartureId,item.supplierDestinationId,item.ticketPrice,item.depTime,item.destTime,item.depStationName)">编辑</span>
                        <span class="dothis" @click="del(item.id)">删除</span>
                    </td>



                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="aaData.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>

        <el-dialog title="新增线路" :visible.sync="addvisible">
            <el-form :model="addForm">
                <el-form-item label="班次" :label-width="formLabelWidth">
                    <el-input v-model="addForm.shiftNum" auto-complete="off" style="width: 400px" placeholder="请输入班次"></el-input>
                </el-form-item>
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-select  v-model="addForm.supplierDepartureId"  placeholder="请选择出发地" @change="depChange"  style="width: 400px">
                        <el-option
                            v-for="item in DepOption"
                            :key="item.depStationCode"
                            :label="item.depStation"
                            :value="item.depStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-select  v-model="addForm.supplierDestinationId"  placeholder="请选择到达地(先选择出发地)" @change="destChange"  style="width: 400px">
                        <el-option
                            v-for="item in DestOption"
                            :key="item.destStationCode"
                            :label="item.destStation"
                            :value="item.destStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出发时间" :label-width="formLabelWidth">
                    <el-time-picker
                        v-model="addForm.depTime"
                        value-format='HH:mm'
                        placeholder="出发时间">
                    </el-time-picker>

                    <!--<el-date-picker-->
                        <!--style="float: left"-->
                        <!--v-model="addForm.depTime"-->
                        <!--type="datetime"-->
                        <!--placeholder="出发时间"-->
                        <!--value-format="yyyy-MM-dd HH:mm:ss"-->
                        <!--@change = "getTimeValue1">-->
                    <!--</el-date-picker>-->

                </el-form-item>
                <el-form-item label="到达时间" :label-width="formLabelWidth">
                    <el-time-picker
                        v-model="addForm.destTime"
                        value-format='HH:mm'
                        placeholder="到达时间">
                    </el-time-picker>
                    <!--<el-date-picker-->
                        <!--style="float: left"-->
                        <!--v-model="addForm.destTime"-->
                        <!--type="datetime"-->
                        <!--placeholder="到达时间"-->
                        <!--value-format="yyyy-MM-dd HH:mm:ss"-->
                        <!--@change = "getTimeValue2"-->
                    <!--&gt;-->
                    <!--</el-date-picker>-->
                </el-form-item>
                <!--<el-form-item label="库存" :label-width="formLabelWidth">-->
                    <!--<el-input v-model="addForm.totalSeatNum" auto-complete="off " style="width: 400px" placeholder="请输入库存"></el-input>-->
                <!--</el-form-item>-->
                <el-form-item label="票价" :label-width="formLabelWidth">
                    <el-input v-model="addForm.ticketPrice" auto-complete="off " style="width: 400px" placeholder="请输入票价"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addSure">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑线路-->
        <el-dialog title="编辑线路" :visible.sync="editvisible">
            <el-form :model="editForm">
                <el-form-item label="班次" :label-width="formLabelWidth">
                    <el-input v-model="editForm.shiftNum" auto-complete="off" style="width: 400px" placeholder="请输入班次"></el-input>
                </el-form-item>
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-select  v-model="editForm.supplierDepartureId"  placeholder="请选择出发地" @change="depChange"  style="width: 400px">
                        <el-option
                            v-for="item in DepOption"
                            :key="item.depStationCode"
                            :label="item.depStation"
                            :value="item.depStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-select  v-model="editForm.supplierDestinationId"  placeholder="请选择到达地(先选择出发地)" @change="destChange"  style="width: 400px">
                        <el-option
                            v-for="item in DestOption"
                            :key="item.destStationCode"
                            :label="item.destStation"
                            :value="item.destStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出发时间" :label-width="formLabelWidth">
                    <el-time-picker
                        v-model="editForm.depTime"
                        value-format='HH:mm'
                        placeholder="出发时间">
                    </el-time-picker>
                </el-form-item>
                <el-form-item label="到达时间" :label-width="formLabelWidth">
                    <el-time-picker
                        v-model="editForm.destTime"
                        value-format='HH:mm'
                        placeholder="到达时间">
                    </el-time-picker>
                </el-form-item>
                <!--<el-form-item label="库存" :label-width="formLabelWidth">-->
                    <!--<el-input v-model="editForm.totalSeatNum" auto-complete="off " style="width: 400px" placeholder="请输入库存"></el-input>-->
                <!--</el-form-item>-->
                <el-form-item label="票价" :label-width="formLabelWidth">
                    <el-input v-model="editForm.ticketPrice" auto-complete="off " style="width: 400px" placeholder="请输入票价"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editvisible = false">取 消</el-button>
                <el-button type="primary" @click="editSure">确 定</el-button>
            </div>
        </el-dialog>

        <!--批量生成库存-->
        <el-dialog title="库存维护" :visible.sync="seatvisible">
            <el-form :model="seatForm">

                <el-form-item label="班次" :label-width="formLabelWidth">
                    <el-select  v-model="seatForm.id"  placeholder="请选择出发地"  style="width: 400px">
                        <el-option
                            v-for="item in seatOption"
                            :key="item.id"
                            :label="item.shiftNum"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="库存" :label-width="formLabelWidth">
                     <el-input v-model="seatForm.totalSeatNum" auto-complete="off " style="width: 400px" placeholder="请输入库存"></el-input>
                </el-form-item>
                <el-form-item label="批量生成开始" :label-width="formLabelWidth">
                    <el-date-picker
                        v-model="seatForm.fromDate"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="批量生成结束" :label-width="formLabelWidth">
                    <el-date-picker
                        v-model="seatForm.endDate"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="seatvisible = false">取 消</el-button>
                <el-button type="primary" @click="seatSure">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"

    export default {
        data() {
            return {
                value:'',
                DepOption:"",
                DestOption:"",
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                addvisible:false,
                editvisible:false,
                seatvisible:false,
                from:"",
                to:"",
                addForm: {
                    depTime:"",
                    destTime:"",
                    shiftNum:"",
                    supplierDepartureId	:"",
                    supplierDestinationId:"",
                    ticketPrice	:"",

                },
                editForm:{
                    depTime:"",
                    destTime:"",
                    shiftNum:"",
                    supplierDepartureId	:"",
                    supplierDestinationId:"",
                    ticketPrice	:"",

                    id:""
                },
                searchForm:{
                    shiftNum:""
                },
                seatForm:{
                    fromDate:"",
                    endDate:"",
                    totalSeatNum:"",
                    id:""
                },
                formLabelWidth: '120px',
                seatOption:""
            };
        },
        methods: {


            shipSchSearch:function () {
                var data  = this.searchForm
                data.pageIndex=this.currentPage
                data.pageSize=this.pageSize
                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }

                ajax.shipSchSearch(data,res=>{
                    if(res){
                        this.aaData = res.data.result
                    }
                })
            },

            add(){
                this.DestOption=[]
                this.addvisible = true
            },
            addSure(){
                console.log(this.addForm)
                for(var i in this.addForm){
                    if(String(this.addForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.shipSchAdd(this.addForm,res=>{
                    if(res){
                        this.addvisible=false
                        this.shipSchSearch()
                        this.$message({
                            showClose: true,
                            message: '增加成功',
                            type: 'success'
                        });
                    }
                })
            },
            search:function () {
                this.currentPage = 1
                this.shipSchSearch ()
            },
            seat(){
                this.seatvisible =true
                this.shipSearchAll()
            },
            shipSearchAll(){
                ajax.shipSearchAll({},res=>{
                    if(res){
                        this.seatOption = res.data.result
                    }
                })
            },
            seatSure(){
                for(var i in this.seatForm){
                    if(String(this.seatForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.shipSchUpAddSeat(this.seatForm,res=>{
                    if(res){
                        this.seatvisible=false
                        this.shipSchSearch()
                        this.$message({
                            showClose: true,
                            message: '生成成功',
                            type: 'success'
                        });
                    }
                })
            },
            edit(id,shiftNum,supplierDepartureId,supplierDestinationId,ticketPrice,depTime,destTime,name){

                this.editvisible=true
                this.editForm={
                    depTime:depTime,
                    destTime:destTime,
                    shiftNum:shiftNum,
                    supplierDepartureId	:supplierDepartureId+"",
                    supplierDestinationId:supplierDestinationId+"",
                    ticketPrice	:ticketPrice,
                    id:id
                }
                var destData = {
                    depStationCode:supplierDepartureId,
                    depStationName:name
                }
                this.getDestList(destData)
            },
            editSure(){
                for(var i in this.editForm){
                    if(String(this.editForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.shipSchUpdate(this.editForm,res=>{
                    if(res){
                        this.editvisible=false
                        this.shipSchSearch()
                        this.$message({
                            showClose: true,
                            message: '编辑成功',
                            type: 'success'
                        });
                    }
                })
            },
            del(id){
                this.$confirm('是否继续删除操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.shipSchDelete({id:id},res=>{
                        if(res){
                            this.shipSchSearch()
                            this.$message({
                                showClose: true,
                                message: '删除成功',
                                type: 'success'
                            });
                        }
                    })

                }).catch(() => {

                });
            },
            getDestList(param){
                ajax.getDestList(param,res=>{
                    if(res){
                        this.DestOption = res.data.result
                    }
                })
            },
            getDepList(){
                ajax.getDepList({vehicleType:2},res=>{
                    if(res){
                        this.DepOption = res.data.result
                    }

                })
            },
            depChange(a){
                var datas={}
                for(var i=0;i<this.DepOption.length;i++){
                    if(this.DepOption[i].depStationCode==a){
                        datas.depStationCode=a
                        datas.depStationName=this.DepOption[i].depStation
                    }
                }
                this.getDestList(datas)
            },
            destChange(){
                console.log("000")
            },
            handleSizeChange(val) {
                this.pageSize = val
                this.shipSchSearch ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.shipSchSearch ()
            }

        },
        mounted(){
            this.getDepList()
            this.shipSchSearch()

         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



